import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import teams from './views/nav1/Teams.vue'
import Config from './views/nav2/Config.vue'
import chals from './views/nav1/Chals.vue'
import Admins from './views/nav2/Admins.vue'
import instances from './views/nav1/Instances.vue'

import echarts from './views/charts/echarts.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/main', component: Main, name: '', hidden: true },
            { path: '/instances', component: instances, name: '',iconCls:'el-icon-message' },

            /*//{ path: '/form', component: Form, name: 'chal' },
            { path: '/chals', component: chals, name: 'chal' },
            { path: '/instances', component: instances, name: '' }*/
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            /*{ path: '/administrators', component: Admins, name: '' }*/
            { path: '/config', component: Config, name: '' },
            //{ path: '/page5', component: Page5, name: '5' }
        ]
    },
   /* {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            { path: '/page6', component: Page6, name: '' }
        ]
    },*/
    {
        path: '/',
        component: Home,
        name: 'Charts',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/echarts', component: echarts, name: '' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


